/* eslint-disable @typescript-eslint/no-unused-vars */
import { Component, Event, EventEmitter, Watch, Method, Prop, State, h } from '@stencil/core';
import { v4 } from 'uuid';
import { SecurityService, TrackingService, LicenseService } from '../../services';
import { ERRORS } from '../../state/constants';
import { AssetsType, ExportData, TrackingStatus, TrackingSteps, TypeFamily, LicenseStatus, ComponentsDto, LicenseComponents, ParametersTracking } from '../../state/model';
import state from '../../state/store';

@Component({
  tag: 'facephi-sdk-provider',
  styleUrl: 'style.css',
  shadow: true,
})
export class SdkProvider {
  private trackingService: TrackingService;
  private securityService: SecurityService;
  private licenseService: LicenseService;

  @State() loading = true;
  @State() operationId: string;
  @State() components: ComponentsDto;
  @State() error: string;
  @State() trackingParameters: ParametersTracking;

  /** Specify the value that identifies the customer of the operation, it can be an ID, an email, a user identifier or any other value that helps you to identify it in your process */
  @Prop() customerId: string;
  /** (required) SDK license. To be provided by Facephi */
  @Prop() apikey: string;
  /** There are currently two types of operations executed: ONBOARDING or AUTENTHICATION default is Onboarding.  */
  @Prop() type: TypeFamily = TypeFamily.onboarding;
  /** Steps of the tracking flow are typed in the exported TrackingSteps enum.  */
  @Prop() steps: TrackingSteps[] | string;
  /** Allows you to disabled tracking */
  @Prop() disabled: boolean;
  /** Relative path where the relative resources are located  */
  @Prop() bundlePath: string;

  /** Event in charge of emitting the operationId when it is generated. */
  @Event() emitExtraData: EventEmitter<string>;
  /** Event in charge of emitting the operationId when it is generated. */
  @Event() emitOperationId: EventEmitter<string>;
  /** Event in charge of emitting the sessionId when it is generated. */
  @Event() emitSessionId: EventEmitter<string>;
  /** Event in charge of emitting the sessionId, the operationId and extraData when it is generated. */
  @Event() emitData: EventEmitter<ExportData>;

  @Watch('operationId')
  watchOperationId(newValue) {
    state.operationId = newValue;
  }

  @Watch('components')
  watchComponents(components: ComponentsDto) {
    if (components) {
      state.components = components;
      const tracking = components[LicenseComponents.tracking];

      this.getComponents();

      if (tracking && !this.disabled) {
        this.trackingParameters = tracking.parameters as ParametersTracking;
        state.tenantId = this.trackingParameters.tenantId;

        this.trackingService = new TrackingService({
          authUrl: `${this.trackingParameters.apiUrl}/auth/realms/inphinite/protocol/openid-connect/token`,
          clientId: this.trackingParameters.clientId,
          clientSecret: this.trackingParameters.clientSecret,
          trackingUrl: `${this.trackingParameters.apiUrl}/api/tracking/events`,
          trackingUrlAssets: `${this.trackingParameters.apiUrl}/api/assets/`,
          tenantId: this.trackingParameters.tenantId,
          customerId: this.customerId || v4(),
          type: this.type || TypeFamily.onboarding,
          bundlePath: this.bundlePath,
        });

        this.initTracking();
      } else {
        this.operationId = v4();
        this.loading = false;
      }
    }
  }

  constructor() {
    if (!this.apikey) {
      console.warn('ApiKey prop is required');
    } else {
      this.securityService = new SecurityService(this.bundlePath);
    }
  }

  async initTracking() {
    this.loading = false;
    await this.trackingService.authenticate();

    const { operationId, sessionId } = await this.trackingService.trackingStart(this.steps, this.customerId);

    this.operationId = operationId;

    this.emitOperationId.emit(this.operationId);
    this.emitSessionId.emit(sessionId);

    this.securityService
      .generateExtraData({
        customerId: this.customerId,
        operationId: operationId,
        sessionId: sessionId,
        trackingFamily: this.type,
        trackingOptions: {
          tenantId: this.trackingParameters.tenantId,
          clientId: this.trackingParameters.clientId,
          clientSecret: this.trackingParameters.clientSecret,
          authUrl: this.trackingParameters.apiUrl,
        },
      })
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      .then((data: any) => {
        this.emitExtraData.emit(data);
        this.emitData.emit({
          operationId,
          extraData: data,
          sessionId,
        });
      });
  }

  async componentWillLoad() {
    this.licenseService = new LicenseService();
    const license = await this.licenseService.getLicense(this.apikey).catch(error => {
      this.error = error.message;
      return false;
    });

    this.securityService.init(async () => {
      if (license) {
        const response = await this.securityService.validLicense(license).catch(() => {
          this.error = ERRORS.LICENSE_INVALID;
        });

        if (response === LicenseStatus.valid) {
          this.components = await this.securityService.getEnabledComponents(license);
        }
      }
    });
  }

  @Method()
  async trackingEvent(screen: string, value: string) {
    this.trackingService && this.trackingService.trackingEvent(screen, value);
  }

  @Method()
  async getComponents() {
    return this.components;
  }

  @Method()
  async trackingAsset(asset: string, documentType: AssetsType) {
    this.trackingService && this.trackingService.trackingAsset(asset, documentType);
  }

  @Method()
  async trackingStatus(status: TrackingStatus, reason?: string) {
    this.trackingService && this.trackingService.trackingStatus(status, reason);
  }

  @Method()
  async getOperationId() {
    return this.operationId;
  }

  render() {
    return this.loading ? this.error ? <p>{this.error}</p> : <div></div> : <slot />;
  }
}
