import * as buffer from 'buffer';
import { AssetsType } from '../state/model';
import { getExtension, getMimeType, urltoFile } from '../utils/file';

const Buffer = buffer.Buffer;

export enum RequestMethods {
  post = 'post',
  get = 'get',
}

export type RequestData = {
  [key: string]: string | boolean;
};

export type RequestOptions = {
  method: RequestMethods;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  data?: any;
};

export type RequestFileData = {
  type: AssetsType;
  family: string;
  tenantId: string;
  operationId: string;
};

export type ApiServiceArguments = {
  authUrl: string;
};

export class ApiService {
  private token: string;
  private authUrl: string;

  constructor(args: ApiServiceArguments) {
    this.authUrl = args.authUrl;
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private getAuthHeader = () => (this.token ? { Authorization: `Bearer ${this.token}` } : {}) as any;

  public setToken(token: string) {
    this.token = token;
  }

  public request = (url: string, options: RequestOptions) => {
    return fetch(url, {
      method: options.method,
      mode: 'cors',
      body: JSON.stringify(options.data),
      headers: {
        Authorization: `Bearer ${this.token}`,
      },
    })
      .then(data => data.json())
      .catch(error => {
        throw new Error(error);
      });
  };

  public requestUpload = async (url: string, file: string, data: RequestFileData) => {
    const contentType = getMimeType(file) || 'image/jpeg';
    const extension = getExtension(contentType);

    const fileUrl = await urltoFile(file, `asset.${extension}'`, contentType);

    return fetch(url, {
      method: RequestMethods.post,
      body: fileUrl,
      mode: 'cors',
      headers: {
        ...this.getAuthHeader(),
        'x-inphinite-asset-type': data.type,
        'x-inphinite-event-family': data.family,
        'x-inphinite-asset-content-type': contentType,
        'x-inphinite-tenant-id': data.tenantId,
        'x-inphinite-event-operation-id': data.operationId,
        'Content-Type': 'application/octet-stream',
      },
    })
      .then(data => data.json())
      .catch(error => {
        throw new Error(error);
      });
  };

  public requestToken: (clientId: string, clientSecret: string) => Promise<string> = (clientId: string, clientSecret: string) =>
    new Promise((resolve, reject) => {
      const params = new URLSearchParams();
      params.append('grant_type', 'client_credentials');

      fetch(this.authUrl, {
        method: RequestMethods.post,
        body: params,
        mode: 'cors',
        headers: {
          'Authorization': 'Basic ' + Buffer.from(`${clientId}:${clientSecret}`).toString('base64'),
          'Accept': '*/*',
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      })
        .then(async data => {
          const response: any = await data.json();

          this.token = response.access_token;
          resolve(response.access_token);
        })
        .catch(error => {
          reject(error);
        });
    });
}
