import { BehaviorSubject } from 'rxjs';
import { TypeFamily, TrackingOptions, LicenseComponents, ComponentsDto } from '../state/model';

export type ExtraDataDto = {
  customerId: string;
  operationId: string;
  trackingFamily: TypeFamily;
  trackingOptions: TrackingOptions;
  sessionId: string;
};

export class SecurityService {
  public myData: BehaviorSubject<string> = new BehaviorSubject<string>('');
  public statusLicense: BehaviorSubject<string> = new BehaviorSubject<string>('');
  public myLicense: BehaviorSubject<ComponentsDto | null> = new BehaviorSubject<ComponentsDto | null>(null);
  private innerWorker: Worker | undefined;
  loaded = false;
  extraData: string;
  private bundlePath: string;

  constructor(bundlePath: string) {
    this.bundlePath = bundlePath;
  }

  init = callback => {
    if (!this.loaded && !this.innerWorker) {
      const createWorker = () =>
        new Worker(new URL(`${window.location.origin}${this.bundlePath}/addon/FBWorker.js`), {
          type: 'module',
        });

      this.innerWorker = createWorker();

      this.innerWorker.onmessage = e => {
        switch (e.data.message) {
          case 'initialized':
            this.loaded = true;
            callback && callback();
            break;
          case 'encryptedData':
            this.myData.next(e.data.data);
            break;
        }
      };
    }
  };

  async validLicense(license) {
    return new Promise((resolve, reject) => {
      this.innerWorker.postMessage({
        message: 'validLicense',
        data: license,
      });

      this.innerWorker.onmessage = e => {
        switch (e.data.message) {
          case 'invalidLicense': {
            console.error('The license is invalid, please contact Facephi for more information');
            reject('invalid license');
            break;
          }

          case 'isValidLicense': {
            resolve(e.data.data);
            break;
          }
        }
      };
    });
  }

  getEnabledComponents(license): Promise<ComponentsDto> {
    return new Promise(resolve => {
      this.innerWorker.postMessage({
        message: 'getEnabledComponents',
        license,
        componentsName: [
          LicenseComponents.tracking,
          LicenseComponents.selphiWidget,
          LicenseComponents.selphidWidget,
          LicenseComponents.videoRecording,
          LicenseComponents.videoRecruitment,
        ],
      });

      this.innerWorker.onmessage = e => {
        switch (e.data.message) {
          case 'getEnabledComponents': {
            resolve(e.data.data);
            break;
          }
        }
      };
    });
  }

  async generateExtraData({ customerId, operationId, trackingFamily, trackingOptions, sessionId }: ExtraDataDto) {
    return new Promise(resolve => {
      if (customerId && operationId && trackingFamily && sessionId && trackingOptions && this.innerWorker) {
        const domain = new URL(trackingOptions?.authUrl);

        this.innerWorker.postMessage({
          message: 'extraData',
          data: {
            customerId,
            operationId,
            FACEPHI_SELPHID_TRACKING_TENANT_UUID: trackingOptions?.tenantId,
            FACEPHI_SELPHID_TRACKING_USER_KEY: trackingOptions?.clientId,
            FACEPHI_SELPHID_TRACKING_URL_KEY: domain.origin,
            FACEPHI_SELPHID_TRACKING_PASSWORD_KEY: trackingOptions?.clientSecret,
            sessionId,
            family: trackingFamily,
            FACEPHI_SELPHID_TRACKING_ASSETS_ENDPOINT: '/api/assets/',
            FACEPHI_SELPHID_TRACKING_EVENTS_ENDPOINT: '/api/tracking/events',
            FACEPHI_SELPHID_TRACKING_AUTH_ENDPOINT: '/auth/realms/inphinite/protocol/openid-connect/token',
            FACEPHI_SELPHID_TRACKING_EVENT_SOURCE: 'backend.sdk',
          },
        });
      }

      this.innerWorker.onmessage = e => {
        switch (e.data.message) {
          case 'encryptedData': {
            resolve(e.data.data);
            break;
          }
        }
      };
    });
  }
}
