import { ERRORS } from '../state/constants';

export class LicenseService {
  getLicense(apiKey: string) {
    return fetch(process.env.LICENSE_URL, {
      mode: 'cors',
      method: 'GET',
      headers: {
        'x-api-key': apiKey,
        'Content-Type': 'application/json',
      },
    })
      .then(async data => {
        const license = await data.json();

        if (new Date(license.dateEnd).getTime() < new Date().getTime()) {
          console.error(ERRORS.LICENSE_EXPIRED);
          throw new Error(ERRORS.LICENSE_EXPIRED);
        }
        return license;
      })
      .catch(error => {
        throw new Error(error.message === 'Failed to fetch' ? ERRORS.LICENSE_SERVICE_ERROR : error.message);
      });
  }
}
