export function urltoFile(url: string, filename: string, mimeType: string) {
  return fetch(url)
    .then(function (res) {
      return res.arrayBuffer();
    })
    .then(function (buf) {
      return new File([buf], filename, { type: mimeType });
    });
}

export function getMimeType(file: string) {
  return file.match(/[^:]\w+\/[\w-+\d.]+(?=;|,)/)?.[0];
}

export function getExtension(contentType: string) {
  return contentType.split('/')[1];
}
