import { v4 } from 'uuid';
import { ApiService, RequestMethods } from '../services';
import { AssetsType, EventsType, TrackingStatus, TrackingSteps, TypePayload } from '../state/model';
import { useDeviceInfo } from '../utils/browser';

export type TrackingServiceArguments = {
  authUrl: string;
  trackingUrlAssets: string;
  trackingUrl: string;
  type: string;
  tenantId: string;
  clientId: string;
  clientSecret: string;
  customerId: string;
  bundlePath;
};

export class TrackingService {
  private apiService;
  private trackingUrlAssets;
  private type;
  private tenantId;
  private trackingUrl;
  private sessionId;
  private clientId;
  private clientSecret;
  private browser;
  private os;
  private customerId;

  constructor(args: TrackingServiceArguments) {
    this.apiService = new ApiService({
      authUrl: args.authUrl,
    });

    this.trackingUrlAssets = args.trackingUrlAssets;
    this.type = args.type;
    this.tenantId = args.tenantId;
    this.trackingUrl = args.trackingUrl;
    this.sessionId = v4();
    this.clientId = args.clientId;
    this.clientSecret = args.clientSecret;
    this.browser = useDeviceInfo().browser;
    this.os = useDeviceInfo().os;
    this.customerId = args.customerId;
  }

  operationId: string;

  private getDefaultData = () => {
    return {
      version: 1,
      tenantId: this.tenantId,
      sessionId: this.sessionId,
      source: this.clientId,
      family: this.type,
    };
  };

  private generateOperationId = (): Promise<string> =>
    new Promise(resolve => {
      const id = v4();
      this.operationId = id;
      resolve(id);
    });

  public trackingAsset = async (asset: string, documentType: AssetsType, id?: string) => {
    const response = await this.apiService.requestUpload(this.trackingUrlAssets, asset, {
      family: this.type,
      tenantId: this.tenantId,
      operationId: id || this.operationId,
      type: documentType,
    });

    await this.apiService.request(this.trackingUrl, {
      method: RequestMethods.post,
      data: {
        operationId: id || this.operationId,
        ...this.getDefaultData(),
        events: [
          {
            eventId: v4(),
            clientTimestamp: new Date().getTime(),
            payload: {
              type: EventsType.asset,
              assetType: documentType,
              contentType: response.contentType,
              url: response.path,
              source: this.getDefaultData().source,
            },
          },
        ],
      },
    });
  };

  public trackingStatus = (status: TrackingStatus, reason?: string, id?: string) => {
    this.apiService.request(this.trackingUrl, {
      method: RequestMethods.post,
      data: {
        ...this.getDefaultData(),
        operationId: id || this.operationId,
        events: [
          {
            eventId: v4(),
            clientTimestamp: new Date().getTime(),
            payload: {
              type: TypePayload.result,
              status,
              reason,
            },
          },
        ],
      },
    });
  };

  public trackingCustomerId = (customerId: string, id?: string) => {
    this.apiService.request(this.trackingUrl, {
      method: RequestMethods.post,
      data: {
        operationId: id || this.operationId,
        ...this.getDefaultData(),
        events: [
          {
            eventId: v4(),
            clientTimestamp: new Date().getTime(),
            payload: {
              type: this.type,
              screen: null,
              event: EventsType.setCustomerId,
              value: customerId,
            },
          },
        ],
      },
    });
  };

  public trackingEvent = (screen: string, value: string, id?: string) => {
    this.apiService.request(this.trackingUrl, {
      method: RequestMethods.post,
      data: {
        operationId: id || this.operationId,
        ...this.getDefaultData(),
        events: [
          {
            eventId: v4(),
            clientTimestamp: new Date().getTime(),
            payload: {
              type: this.type,
              screen,
              event: EventsType.stepChange,
              value,
            },
          },
        ],
      },
    });
  };

  public authenticate = async () => {
    const token = await this.apiService.requestToken(this.clientId, this.clientSecret);
    this.apiService.setToken(token);
    return token;
  };

  public trackingStart = async (steps?: string | TrackingSteps[], email?: string) => {
    const operationId = await this.generateOperationId();
    const events = [];

    events.push({
      eventId: v4(),
      clientTimestamp: new Date().getTime(),
      payload: {
        type: this.type,
        screen: 'START',
        event: EventsType.stepChange,
        value: 'START',
      },
    });

    events.push({
      eventId: v4(),
      clientTimestamp: new Date().getTime(),
      payload: {
        type: TypePayload.device,
        osVersion: this.browser.version,
        model: this.browser.version,
        brand: this.browser.name,
        browser: this.browser.name,
        osName: this.os.name,
      },
    });

    if (steps) {
      events.push({
        eventId: v4(),
        clientTimestamp: new Date().getTime(),
        payload: {
          type: this.type,
          screen: 'START',
          event: EventsType.stepFlows,
          value: steps,
        },
      });
    }

    if (email) {
      events.push({
        eventId: v4(),
        clientTimestamp: new Date().getTime(),
        payload: {
          type: this.type,
          screen: null,
          event: EventsType.setCustomerId,
          value: email ? email : this.customerId,
        },
      });
    }

    await this.apiService.request(this.trackingUrl, {
      method: RequestMethods.post,
      data: {
        operationId: this.operationId,
        ...this.getDefaultData(),
        events,
      },
    });

    return {
      operationId,
      sessionId: this.sessionId,
    };
  };
}
